<template>
  <div class="circle_propress" :style="style">
    <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
      <circle
        :cy="circle.cy"
        :cx="circle.cx"
        :r="circle.r"
        :stroke="props.progressBgColor"
        :stroke-width="props.strokeWidth"
        fill="none"
      />
      <circle
        :cy="circle.cy"
        :cx="circle.cx"
        :r="circle.r"
        fill="none"
        :stroke="props.progressColor"
        :transform="`rotate(-90 ${circle.cx} ${circle.cy})`"
        :stroke-width="props.strokeWidth"
        :stroke-dasharray="perimeter"
        :stroke-dashoffset="batterypercent"
        :stroke-linecap="props.strokeLinecap"
      ></circle>
    </svg>
    <div class="percent_text">
      <slot name="percent">{{ percentValue }}%</slot>
    </div>
  </div>
</template>
<script setup>
import { ref, computed, watchEffect } from "vue";
const props = defineProps({
  size: {
    type: Number,
    default: 300,
  },
  strokeWidth: {
    type: Number,
    default: 5,
  },
  percent: {
    type: Number,
    default: 20,
  },
  progressBgColor: {
    type: String,
    default: "#eee",
  },
  progressColor: {
    type: String,
    default: "#25b489",
  },
  strokeLinecap: {
    type: String,
    default: "round",
  },
  part: {
    type: Number,
    default: 0,
  },
});
const percentValue = computed(() => {
  return props.percent < 0 ? 0 : props.percent > 100 ? 100 : props.percent;
});
const style = computed(() => {
  return { width: props.size + "px", height: props.size + "px" };
});
const circle = computed(() => {
  return {
    cx: props.size / 2,
    cy: props.size / 2,
    r: props.size / 2 - props.strokeWidth / 2,
  };
});
const perimeter = computed(() => {
  return 2 * Math.PI * circle.value.r;
});
const batterypercent = computed(() => {
  return (1 - percentValue.value / 100) * perimeter.value;
});
</script>
<style lang="scss" scoped>
.circle_propress {
  width: 300px;
  height: 300px;
  text-align: center;
  position: relative;
  .percent_text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>