<template>
  <div class="svg_progress">
    <div class="svg-item">
      <circlePropress :size="size" :percent="percent" :strokeWidth="8">
      </circlePropress>
      <ball-rolling
        :stroke-width="5"
        :roll-length="50"
        :time="100"
      ></ball-rolling>
      <svg-icon name="test"></svg-icon>
      <part-progress
        :stroke-width="20"
        :part="100"
        :percent="percent"
        :size="100"
      ></part-progress>
    </div>
  </div>
</template>
<script setup>
import circlePropress from "./components/circlePropress.vue";
import ballRolling from "./components/ballRolling.vue";
import partProgress from "./components/partProgress.vue";
import { ref, onMounted } from "vue";
const percent = ref(25);
const size = ref(300);
const loopTime = ref();

onMounted(() => {
  loadingRrogress();
});

const loadingRrogress = () => {
  if (percent.value < 100) {
    let time = Math.random() * 100;
    loopTime.value = setTimeout(() => {
      percent.value += 1;
      loadingRrogress();
    }, time);
  } else {
    clearTimeout(loopTime.value);
  }
};
</script>
<style lang="scss" scoped>
.svg_progress {
  .svg-item {
    text-align: center;
  }
}
</style>