<template>
  <div class="part_progress" :style="style">
    <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
      <defs>
        <linearGradient x1="0%" y1="0%" x2="100%" y2="100%" id="gradient">
          <stop offset="0%" style="stop-color: #fe0808; stop-opacity: 1" />
          <stop offset="100%" style="stop-color: #0dfc71; stop-opacity: 1" />
        </linearGradient>
      </defs>
      <circle
        :cy="circle.cy"
        :cx="circle.cx"
        :r="circle.r"
        :stroke="props.progressBgColor"
        :stroke-width="props.strokeWidth"
        fill="none"
      />
      <circle
        :cy="circle.cy"
        :cx="circle.cx"
        :r="circle.r"
        fill="none"
        stroke="url(#gradient)"
        :transform="`rotate(-90 ${circle.cx} ${circle.cy})`"
        :stroke-width="props.strokeWidth"
        :stroke-linecap="props.strokeLinecap"
        :stroke-dasharray="dasharray"
      ></circle>
      <circle
        :cy="circle.cy"
        :cx="circle.cx"
        :r="circle.r"
        fill="none"
        :transform="`rotate(-90 ${circle.cx} ${circle.cy})`"
        :stroke="props.progressBgColor"
        :stroke-width="props.strokeWidth + 2"
        :stroke-dasharray="dasharray1"
        :stroke-dashoffset="batterypercent"
      ></circle>
    </svg>
  </div>
</template>
<script setup>
import { computed, ref } from "vue";
const props = defineProps({
  size: {
    type: Number,
    default: 200,
  },
  progressBgColor: {
    type: String,
    default: "#eee",
  },
  strokeWidth: {
    type: Number,
    default: 5,
  },
  progressColor: {
    type: String,
    default: "#25b489",
  },
  strokeLinecap: {
    type: String,
    default: "butt",
  },
  part: {
    type: Number,
    default: 10,
  },
  percent: {
    type: Number,
    default: 10,
  },
});
const style = computed(() => {
  return { width: props.size + "px", height: props.size + "px" };
});
const circle = computed(() => {
  return {
    cx: props.size / 2,
    cy: props.size / 2,
    r: props.size / 2 - props.strokeWidth / 2,
  };
});
const perimeter = computed(() => {
  return 2 * Math.PI * 90;
});
const dasharray = computed(() => {
  let perimeter1 = perimeter.value / props.part;
  return `${perimeter1 / 2} ${perimeter1 / 2}`;
});
const dasharray1 = computed(() => {
  return `${perimeter.value}`;
});
const percentValue = computed(() => {
  return props.percent < 0 ? 0 : props.percent > 100 ? 100 : props.percent;
});
const batterypercent = computed(() => {
  // let percentValue = (perimeter.value * props.percent) / 100;
  // return `-${perimeter - percentValue}`;
  return -(percentValue.value / 100) * perimeter.value;
});
</script>
<style lang="scss" scoped>
.part_progress {
}
</style>